import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';

@Component({
    selector: 'app-using-documents',
    templateUrl: './using-documents.component.html',
    styleUrls: ['./using-documents.component.scss']
})
export class UsingDocumentsComponent implements OnInit {
    paramsList: Array<any> = [];

    constructor(
        public location: Location
    ) { }

    ngOnInit() {
        this.paramsList = [
            {
                name: 'selects',
                explain: '',
                type: 'Array<{text:String,value:String,disabled?:Boolean}>',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'model',
                explain: '[(model)]=\'selectValue1\'',
                type: '',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'disabled',
                explain: '',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'false'
            },
            {
                name: 'clear',
                explain: '',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'false'
            },
            {
                name: 'item',
                explain: 'itemdisabledtrue',
                type: '',
                optionaValue: '',
                defaultValue: '-'
            }
        ];
    };
};



