import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-select-demo',
    templateUrl: './select-demo.component.html',
    styleUrls: ['./select-demo.component.scss']
})

export class SelectDemoComponent implements OnInit {
    selects1: Array<any> = [];
    selects2: Array<any> = [];
    selects3: Array<any> = [];
    selects4: Array<any> = [];
    selects5: Array<any> = [];

    selectValue1: any;
    selectValue2: any;
    selectValue3: any;
    selectValue4: any = '03';
    selectValue5: any;

    constructor() { };

    ngOnInit() {
        this.selects1 = [
            { text: '', value: '01' },
            { text: '', value: '02' },
            { text: '', value: '03' },
            { text: '', value: '04' },
            { text: '', value: '05' },
            { text: '', value: '06' }
        ];

        this.selects2 = [
            { text: '0', value: '01' },
            { text: '0', value: '02' },
            { text: '0', value: '03' },
            { text: '0', value: '04' },
            { text: '0', value: '05' },
            { text: '0', value: '06' }
        ];

        this.selects3 = [
            { text: 'A', value: '01' },
            { text: 'B', value: '02', disabled: true },
            { text: 'C', value: '03' },
            { text: 'D', value: '04' },
            { text: 'E', value: '05' },
            { text: 'F', value: '06' }
        ];

        this.selects4 = [
            { text: '', value: '01' },
            { text: '', value: '02' },
            { text: '', value: '03' },
            { text: '', value: '04' },
            { text: '', value: '05' },
            { text: '', value: '06' }
        ];

        this.selects5 = [
            { text: '', value: '01' },
            { text: '', value: '02' },
            { text: '', value: '03' },
            { text: '', value: '04' },
            { text: '', value: '05' },
            { text: '', value: '06' }
        ];

    };

}



